header {
    background-color: var(--body_background);
}
.nav {
    background-color: var(--body_background);
    align-items: center;
    position: fixed;
    width: 100%;
    left: 0;
    top: 0;
}

.nav_text {
    color: var(--link_color);
    font-family: "Lobster", sans-serif;
    font-weight: 400;
    font-style: normal;
}

.nav_text :hover {
    color: green;
}
.head_txt {
    color: var(--link_color);
    font-family: "Lobster", sans-serif;
    font-weight: 400;
    font-style: normal;
}